	module.exports = {"data":{"challengeNode":{"title":"Place Items in Grid Areas Using the grid-area Property","description":["像上一个挑战那样，在为网格容添加区域模板后，你可以通过添加你定义的名称将网格项放入自定义区域。为此，你需要对网格项使用<code>grid-area</code>：","<blockquote>.item1 { grid-area: header; }</blockquote>","这样，类名为<code>item1</code>的网格项就被放到了<code>header</code>区域里。这种情况下，网格项将使用整个顶行，因为这一行被名为 header 区域。","<hr>","请使用<code>grid-area</code>属性，把类为<code>item5</code>元素放到<code>footer</code>区域。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-grid/place-items-in-grid-areas-using-the-grid-area-property","blockName":"CSS Grid","tests":[{"text":"<code>item5</code>类应该有<code>grid-area</code>属性且值为<code>footer</code>。","testString":"assert(code.match(/.item5\\s*?{[\\s\\S]*grid-area\\s*?:\\s*?footer\\s*?;[\\s\\S]*}/gi), '<code>item5</code>类应该有<code>grid-area</code>属性且值为<code>footer</code>。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .item1{background:LightSkyBlue;}\n  .item2{background:LightSalmon;}\n  .item3{background:PaleTurquoise;}\n  .item4{background:LightPink;}\n  \n  .item5 {\n    background: PaleGreen;\n    /* 请在本行以下添加你的代码 */\n    \n    \n    /* 请在本行以上添加你的代码 */\n  }\n  \n  .container {\n    font-size: 40px;\n    min-height: 300px;\n    width: 100%;\n    background: LightGray;\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n    grid-template-rows: 1fr 1fr 1fr;\n    grid-gap: 10px;\n    grid-template-areas: \n      \"header header header\"\n      \"advert content content\"\n      \"footer footer footer\";\n  }\n</style>\n  \n<div class=\"container\">\n  <div class=\"item1\">1</div>\n  <div class=\"item2\">2</div>\n  <div class=\"item3\">3</div>\n  <div class=\"item4\">4</div>\n  <div class=\"item5\">5</div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-grid/use-grid-area-without-creating-an-areas-template","id":"5a94fe1369fb03452672e45d"},"slug":"/responsive-web-design/css-grid/place-items-in-grid-areas-using-the-grid-area-property"}}

